.overall-rating .reviews-box {
    margin: 0;
    padding: 0
}

.overall-rating .reviews-box li {
    line-height: 1.2;
    padding: 0;
    list-style: none;
    border: 0;
    box-shadow: none;
    margin: 0 0 10px;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex
}

.overall-rating .reviews-box li span {
    display: inline-block;
    vertical-align: middle
}

.overall-rating .reviews-box li .label {
    font-size: 12px;
    position: relative;
    width: 25px;
    padding: 0;
    text-align: left;
    color: #222
}

.overall-rating .reviews-box li .label i {
    position: absolute;
    top: 0;
    right: -3px;
    display: inline-block
}

.overall-rating .reviews-box li .item-list {
    position: relative;
    height: 10px;
    margin: 0 10px;
    border-radius: 1px;
    background-color: #e6e9ee;
    width: 66%;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1
}

.overall-rating .reviews-box li .item-list span {
    height: 10px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #624615
}

.overall-rating .reviews-box li .label:first-child {
    min-width: 10px;
    width: auto
}

.overall-rating .reviews-box li .label:last-child {
    width: 30px
}

.overall-rating,.ratings-summary {
    display: inline-block;
    vertical-align: middle
}

.ratings-summary {
    background-color: #f8f8f8;
    text-align: center;
    color: #222;
    border: 1px solid #ddd;
    height: 100px;
    margin-right: 30px;
    width: 200px
}

.overall-rating {
    width: 280px
}

.ratings-summary .ratings-average {
    font-size: 40px;
    font-weight: 700;
    line-height: 1
}

.reviews-count {
    margin-top: 30px
}

.reviews-list .media-body {
    border-bottom: 1px solid #ddd
}

.review-date {
    font-size: 12px;
    font-weight: 400;
    display: inline-block
}

.review-date:before {
    content: "";
    font-family: fontawesome;
    display: inline-block;
    color: #624615;
    margin-right: 5px
}

.rating-wrap {
    display: inline-block;
    float: right
}

.reviews-list {
    margin-bottom: 30px
}

.reviews-list .media-left {
    padding-right: 20px
}

.review-content {
    padding-top: 20px
}

@media (max-width: 600px) {
    .overall-rating,.ratings-summary {
        margin-left:auto;
        margin-right: auto;
        display: block;
        width: 200px
    }

    .aggregate-rating {
        text-align: center
    }
}

@media (max-width: 320px) {
    .rating-wrap {
        float:none
    }

    .overall-rating,.ratings-summary {
        width: 100%
    }
}

.login-for-review {
    text-decoration: underline;
    color: #624615
}

.waiting-for-approval {
    color: #624615
}

.content-single-property .panel-title>a {
    display: block
}

.single-property-element {
    margin-bottom: 60px
}

@media screen and (max-width: 768px) {
    .single-property-element {
        margin-bottom:40px
    }
}

.single-property-element .agent-info {
    margin-bottom: 15px
}

.single-property-element .agent-info i {
    width: 20px;
    display: inline-block
}

.feature-unchecked {
    opacity: .4
}

.property-feature-wrap {
    margin-bottom: 10px
}
